<template>
    <div class="aData">
        <div class="header">
            <BreadCrumb />
        </div>
        <div class="content">
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <el-form :inline="true" :model="formInline" class="demo-form-inline" :rules="rules" ref="formInline">
                        <el-form-item label="" prop="startDate" >
                            <el-date-picker
                                    style="width: 160px;"
                                    v-model="formInline.startDate"
                                    type="date"
                                    placeholder="">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="endDate">
                            <el-date-picker
                                    style="width: 160px;"
                                    v-model="formInline.endDate"
                                    type="date"
                                    placeholder="">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="region">
                            <el-select v-model="formInline.region" placeholder="" style="width: 120px;">
                                <el-option
                                        v-for="item in items"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="onSubmit('formInline')" ><font-awesome-icon icon="search" pull="left"/></el-button>
                            <!-- <el-button type="warning" @click="onSubmit('formInline')" ><font-awesome-icon icon="print" pull="left"/></el-button> -->
                            <el-button type="danger" @click="onExport()"><font-awesome-icon icon="file-export" pull="left"/></el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <table-pagination
                        id="AlarmDataTable"
                        @handleSizeChange="handleSizeChange"
                        @handleCurrentChange="handleCurrentChange "
                        :data="data"
                        :columns="columns"
                        :pagination="pagination"
                ></table-pagination>
            </el-card>
        </div>
    </div>
</template>

<script>
    import TablePagination from "@/components/yTablePagination"
    import BreadCrumb from "@/components/yBreadCrumb"
    import FileSaver from 'file-saver'
    import XLSX from 'xlsx'

    export default {
        name:"AlarmData",
        components:{
            TablePagination,
            BreadCrumb
        },
        data(){
            return{
                formInline: {
                    startDate: '',
                    endDate: '',
                    region: ''
                },
                items:[
                    {
                        value: '',
                        label: ''
                    },
                    {
                        value: '',
                        label: ''
                    },
                    {
                        value: '',
                        label: ''
                    }
                ],
                rules:{
                    region: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    startDate: [
                        { type: 'date', required: true, message: '', trigger: 'change' }
                    ],
                    endDate: [
                        { type: 'date', required: true, message: '', trigger: 'change' }
                    ]
                },
                columns:[
                    { prop : 'title' , label: '' },
                    { prop : 'create_name', label: ''},
                    { prop : 'item_name', label: ''},
                    { prop : 'create_time', label: ''},
                    { prop : 'weight', label: ''},
                ],
                data:[],
                pagination: {
                    total:0,
                    currentPage: 1,
                    pageSize: 5,
                    pageSizes:[5, 10, 15, 20]
                },
            }
        },
        created(){
            this.getData();
        },
        methods: {
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            onExport(){
                var wb = XLSX.utils.table_to_book(document.querySelector('#AlarmDataTable'))
                /* get binary string as output */
                var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
                try {
                    FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'AlarmDataTable.xlsx')
                } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
                return wbout
            },
            getData(){
                this.$ajax .get('/api').then(res=>{
                    this.data=res.data.ALARM;
                    this.pagination.total=res.data.ALARM.length
                })
            },
            handleSizeChange (size) {
                this.pagination.pageSize=size;
                console.log(' this.limit:', size)
            },
            // 
            handleCurrentChange (index) {
                this.pagination.currentPage=index;
                console.log(' this.page:', index)
            },
        },
    }
</script>

<style scoped>
    .aData{
        width: 100%;
        height: 100%;
        overflow:hidden;
    }

    .el-form-item{
        margin-bottom: 0;
    }
</style>



